
.home_body_b{width:100%;box-sizing:border-box;padding:0 30px 30px
}
dl.home_dl{width:100%;padding:25px 10px;box-sizing:border-box;line-height:38px;background:white;box-shadow:0 2px 6px 0 #cacaca;border-radius:5px;overflow:hidden;position:relative;margin:0 0 30px
}
dl.home_dl:before{content:'';display:inline-block;position:absolute;top:0;left:0;height:3px;width:100%;background:#CBEBAC
}
dl.home_dl.dl_two:before{content:'';display:inline-block;position:absolute;top:0;left:0;height:3px;width:100%;background:#FFCBBC
}
dl.home_dl.dl_three:before{content:'';display:inline-block;position:absolute;top:0;left:0;height:3px;width:100%;background:#AEE3FF
}
dl.home_dl dt{text-align:center;font-size:18px
}
dl.home_dl dd{text-align:center;color:#FF8100
}
dl.home_dl dd span.data_span{padding-right:6px;font-size:26px
}
dl.home_dl.dl_four{padding:10px 0 11px;height:129px
}
dl.home_dl.dl_four:before{display:none
}
dl.home_dl.dl_four dd{text-align:left;line-height:36px;background:url('../../img/band1.png') left center no-repeat;background-size:auto 25px;padding-left:58px
}
dl.home_dl.dl_four dd:nth-child(2){background:url('../../img/band2.png') left center no-repeat;background-size:auto 25px
}
dl.home_dl.dl_four dd:nth-child(3){background:url('../../img/band3.png') left center no-repeat;background-size:auto 25px
}
dl.home_dl.dl_four dd span{padding-left:10px
}
dl.home_dl.dl_four dd img{width:20px;height:20px;border-radius:100%;vertical-align:middle
}
.line_pic{width:100%;background:white;padding:10px 0;box-sizing:border-box;height:auto;min-height:248px;text-align:left;margin-bottom:30px;border-radius:6px;position:relative
}
.line_pic p.title{width:100%;padding-left:15px;line-height:30px;font-size:16px;position:relative;box-sizing:border-box
}
.line_pic p.title:before{content:'';position:absolute;left:0;width:3px;height:16px;background:#FF8100;top:7px
}
.line_pic p.title.title2:before{content:'';background:#4A90E2
}
.line_pic .select{text-align:right;width:100%;padding-right:30px;box-sizing:border-box;position:relative;z-index:2
}
.line_pic .line_body{width:100%;height:auto;min-height:500px;padding:10px 30px;box-sizing:border-box;margin-top:-30px
}
.line_pic h3.note{width:100%;line-height:25px;text-align:center;padding-top:10px;font-weight:normal
}


