
body,p,ul,ol,li,dl,dt,dd,h1,h2,h3,h4,h5,h6,form,fieldset,legend,input,select,textarea,button,th,td{margin:0;padding:0
}
h1,h2,h3,h4,h5,h6{font-size:100%
}
ul,dl,ol{list-style:none
}
img,fieldset,input[type="submit"]{border:0 none
}
img{display:inline-block;overflow:hidden;vertical-align:top
}
table{border-collapse:collapse;border-spacing:0
}
button,input[type="button"]{cursor:pointer;border:0 none;-webkit-tap-highlight-color:rgba(0,0,0,0);tap-highlight-color:rgba(0,0,0,0)
}
textarea{word-wrap:break-word;resize:none
}
menu{margin:0;padding:0
}
body{-webkit-text-size-adjust:none;font-family:Arial,helvetica,"微软雅黑","Microsoft Yahei",sans-serif;font-family:"Helvetica Neue",Helvetica,Arial,sans-serif
}
body .el-button--primary{color:#fff;background-color:#FF8100;border-color:#FF8100
}
body .el-button--default{color:#FF8100;background-color:white;border-color:#FF8100
}
body .el-date-editor.el-input{width:100%;min-width:193px
}
input[type="number"]{-webkit-user-select:text
}
a,button,input,img{-webkit-touch-callout:none
}
input,select,textarea{outline:none
}
a,button,input,textarea,span{-webkit-tap-highlight-color:rgba(0,0,0,0);tap-highlight-color:rgba(0,0,0,0)
}
a,a:link,a:visited,a:hover{text-decoration:none;color:#606470;cursor:pointer
}
@media all and (-webkit-transform-3d){
a,button,input,textarea,span{-webkit-tap-highlight-color:rgba(0,0,0,0);tap-highlight-color:rgba(0,0,0,0)
}
}
textarea{appearance:none;-webkit-appearance:none;-moz-appearance:none
}
a{text-decoration:none
}
input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;appearance:none;margin:0
}
input[type="number"]{-moz-appearance:textfield
}
input:-webkit-autofill,textarea:-webkit-autofill,select:-webkit-autofill{background-color:white;background-image:none;color:#000000
}
section,div,dl,dt,dd,footer{box-sizing:border-box
}
