
ul.step_ul{width:100%;height:45px;line-height:45px
}
ul.step_ul:after{content:'';clear:both
}
ul.step_ul li{width:25%;float:left;text-align:center;font-size:14px;border-bottom:2px solid #C2C2C2
}
ul.step_ul li.tab{border-bottom:2px solid #FF8100;color:#FF8100
}
.step_show{width:100%;padding:20px 0;box-sizing:border-box
}
.step_show .step_show_inner{width:100%;padding:0 20px;box-sizing:border-box
}
.step_show p.title{font-size:16px;margin-bottom:22px;padding-left:20px;width:100%;position:relative;box-sizing:border-box
}
.step_show p.title:before{content:'';width:3px;height:14px;position:absolute;left:0;background:#FF8100;top:2px
}
.big_pic{width:100%;padding:25px 165px;box-sizing:border-box;text-align:center
}
.big_pic img{width:100%;height:auto
}


